import Loading from '../public/Loading'
import Chat from '../public/chat'
import None from '../public/noNumber'
import '../../static/css/warn/iconfont.css'
import Foot from "../public/foot.vue"
import Head from "../public/head.vue"
import loadingImg from '../public/loadingImg'
  export default {
      name: "jouerney",
    data(){
        return{
          api:this.GLOBAL.baseURL,
          orderList:[],
          overList:[],
          goONList:[],
          activeIndex: -1,
          visible2: false,
          visible3: false,
          checkList:[],
          radio2:'',
          reason:"",
          idea:'',
          unsubscribe:false,
          sliderIndex:0,
          goONIndex:0,
          left:0,
          goLeft:0,
          isOver:false,
          isOrder:false,
          isChat:false,
          imgUrl:'',
          userId:'',
          userName:'',
          userImg:'',
          isGoIng:true,
          isWill:true,
          isPass: true,
          goMore:false,
          jionMore:false,
          takeMore:false,
          goindex:1,
          willIndex:1,
          overIndex:1
        }
    },
    components:{
        Head,
      Foot,
      None,
      loadingImg,
      Chat,
      Loading
    },
    methods:{
      lookMore(val){
        if(val == 1){
          this.getJouney(this.goindex)
        }else if(val == 2){
          this.getGon(this.willIndex)
        }else{
          this.getoverJ(this.overIndex)
        }
      },
        //立即评价
      discuss(item,index){
        localStorage.setItem('dis','Orderevaluate')
        this.$router.push({
          path: '/Orderevaluate',
          query:{
            order_id: item.order_id
          }
        })
      },
      look(item,index){
        this.$router.push({
          path:'/publishPage',
          query:{
            information: item.activity_id
          }
        })
      },
      toPublish(item,index){
        this.$router.push({
          path: '/publishPage',
          query:{
            information: item.activity_id
          }
        })
      },
      refer(){
        this.$message({
          message:'申请退订成功',
          type:'success'
        })
        this.activeIndex = -1
      },
      disputes(){
        this.$router.push('/Disputes')
      },
      getoverJ(val){
        this.$http.post(this.api + '/vipact',{
          token: localStorage.getItem('token'),
          iscomplete: 2,
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              if(res.data.data.data.length){
                for(let i = 0;i<res.data.data.data.length;i++){
                  this.overList.push(res.data.data.data[i])
                }
                this.isOver = false
                this.takeMore = true
                  this.overIndex++
              }else{
                this.isOver = true
                this.takeMore = false
              }
              this.isGoIng = false
              console.log(this.overList)
            }else if(res.data.code == 3){
             this.$http.post(this.api + '/home/index/token')
               .then(res=>{
                 localStorage.setItem('token',res.data.data)
                 this.getoverJ(val)
               })
            }else if(res.data.code == 0){
              this.isGoIng = false
              alert(res.data.msg)
            }
          })
      },
      getGon( val){
        this.$http.post(this.api + '/vipact',{
          token: localStorage.getItem('token'),
          iscomplete: 0,
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              if(res.data.data.data.length){
                for(let i = 0;i<res.data.data.data.length;i++){
                  this.orderList.push(res.data.data.data[i])
                }

                this.isOrder = false
                this.jionMore = true
                 this.willIndex++
              }else{
                this.isOrder = true
                this.jionMore = false
              }
              this.isWill = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getGon(val)
                })
            }else if(res.data.code == 0){
              this.isWill = false
              alert(res.data.msg)
            }
          })
      },
      getJouney( val){
        this.$http.post(this.api + '/vipact',{
          token: localStorage.getItem('token'),
          iscomplete: 1,
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              if(res.data.data.data.length){
                for(let i = 0;i<res.data.data.data.length;i++){
                  this.goONList.push(res.data.data.data[i])
                }
                this.isOrder = false
                this.goMore = true
                this.goindex++
              }else{
                this.isOrder = true
                this.goMore = false
              }
              this.isPass = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getJouney(val)
                })
            }else if(res.data.code == 0){
              this.isPass = false
              alert(res.data.msg)
            }
          })
      },
      prevGoon(){
        this.goONIndex -= 1
        this.goLeft += 360
      },
      nextGoon(){
        this.goONIndex += 1
        this.goLeft -= 360
      },
      prevIndex(){
        this.sliderIndex -= 1
        this.left += 360
      },
      nextIndex(){
        this.sliderIndex += 1
        this.left -= 360
      },
      //联系策划人
      chat(){
        this.isChat = false
      },
      contact(item,index){
        this.userName = item.act_user.family_name + item.act_user.middle_name + item.act_user.name
        this.userId = item.act_user.user_id
        this.userImg = item.act_user.head_image? item.act_user.headimage.domain + item.act_user.headimage.image_url:'../../static/img/static/user.png'
        this.isChat = true
      },
      reload(res){
        let data = res.data.data[0]
        this.isOwer = data.user_id
        this.imgUrl = data.head_image?data.headimage.domain + data.headimage.image_url:'../../static/img/static/user.png'
      }
    },
    mounted(){
        this.getJouney(this.goindex)
      this.getoverJ( this.overIndex)
      this.getGon( this.willIndex)
    }
  }



